* {
  box-sizing: border-box;
}

body, h1, h2, h3, a, li, ul, p {
  margin: 0;
  padding: 0;
}

.page {
  font-family: Arial, sans-serif;
  font-size: 16px;
}

.page__body {
  height: 100vh;
}

.page__navigation {
  padding: 20px 20px 0;
}

.page__main {
  margin: 0 auto;
}

.page__footer {
  width: 100%;
  padding: 0 20px;
  position: fixed;
  bottom: 0;
}

.navigation {
  line-height: 40px;
}

.navigation__container--left {
  float: left;
  height: 100%;
}

.navigation__container--right {
  float: right;
  height: 100%;
}

.navigation__list {
  list-style-type: none;
}

.navigation__item {
  display: inline-block;
}

.navigation__item:not(:last-child) {
  margin-right: 10px;
}

.navigation__link {
  color: #000;
  text-decoration: none;
}

.navigation__link:hover {
  text-decoration: underline;
}

.navigation__link--with-icon {
  vertical-align: middle;
  width: 40px;
  height: 40px;
  display: inline-block;
}

.navigation__link--footer {
  color: #5f6368;
}

.navigation__link-icon {
  height: 100%;
}

.navigation__link-icon:hover {
  filter: contrast(.1);
}

.main {
  text-align: center;
  width: max-content;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

