}

.search-box:hover {
  box-shadow: 1px 2px 5px #d3d3d3;
}

.search-box__icon {
  vertical-align: middle;
  height: 20px;
}

.search-box__icon--clickable {
  cursor: pointer;
}

.search-box__input {
  border: none;
  outline: none;
  width: 400px;
  margin: 0 10px;
}

.buttons {
  width: fit-content;
  margin: 0 auto;
  padding-bottom: 20px;
